 /*  SOCIALS */

 .socials-container ul {
	 display: flex;
	 justify-content: flex-start;
	 gap: 2rem;
	 padding: 0;
	 width: fit-content;
	 margin: 2rem 1rem;
  }
  
  .socials-container ul li {
	list-style: none;
  }
  
  .socials-container ul li a {
	width: 5.5rem;
  	height: 5.5rem;
  	background-color: rgb(209, 211, 228);
	font-size: 3.5rem;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
	border: 3px solid rgb(180, 182, 207);
	z-index: 1;
  }

  @media (max-width: 600px) {
	.socials-container ul {
		width: 100%;
		justify-content: space-evenly;
	}
	.socials-container ul li a {
		background-color: white;
		border: 3px solid rgb(116, 115, 153);
		width: 8rem;
		height: 8rem;
		font-size: 5rem;
	}
  }
  
  .socials-container ul li a .icon {
	position: relative;
	color: #1a1919;
	transition: .5s;
	z-index: 3;
  }
  
  .socials-container ul li a:hover .icon {
	color: #fff;
	transform: rotateY(360deg);
  }
  
  .socials-container ul li a:before {
	content: "";
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	background: #f00;
	transition: .5s;
	z-index: 2;
  }
  
  .socials-container ul li a:hover:before {
	top: 0;
  }
  
  .socials-container ul li:nth-child(1) a:before{
	background: #3b5999;
  }
  
  .socials-container ul li:nth-child(2) a:before{
	background: #4190ce;
  }
  
  .socials-container ul li:nth-child(3) a:before {
	background: #1d923a;
  }
  


  